import { Component, OnInit } from '@angular/core';
import { ApiBoredService } from '../api-bored.service';

@Component({
  selector: 'app-bored',
  templateUrl: './bored.component.html',
  styleUrls: ['./bored.component.css']
})
export class BoredComponent implements OnInit {

  im:string='';
  type:string = '';
  parti = 0;
  key:string='';
  constructor(private _apiMon :ApiBoredService) { }

  ngOnInit(): void {
  }  

  boredFact(){
    let ob = this._apiMon.getBoredFact();
    ob.subscribe((res:any)=>{
      // console.log(res.activity);
      // console.log(res.type);
      // console.log(res.participants);
      
       this.im = res.activity;
       this.type = res.type;
       this.parti = res.participants;
       this.key= res.key;
      
    })
  }

  btnrefresh(){
    this.boredFact();
  }
}
