import { Component, OnInit } from '@angular/core';
import { ApiDeckService } from '../api-deck.service';

@Component({
  selector: 'app-deck',
  templateUrl: './deck.component.html',
  styleUrls: ['./deck.component.css']
})
export class DeckComponent implements OnInit {

  suffle:boolean=true;
  nme:string='';
  remaing:number=0;
  constructor(private _apiDeck :ApiDeckService) { }

  ngOnInit(): void {
  }

  CatImg(){
    let ob = this._apiDeck.getAgify().subscribe((res:any)=>{
      console.log(res);

      this.suffle=res.shuffled;
      this.nme = res.deck_id;
      this.remaing=res.remaining;

      // console.log(res.deck_id);
    })
   }

   btnrefresh(){
     this.CatImg();
   }

}
